import React, { Component ,Fragment} from 'react'
import gql from 'graphql-tag'
import ApolloClient from 'apollo-boost'
import { Query } from '@apollo/react-components';
import LaunchesItem from './LaunchesItem';
import MissionKey from './MissionKey';
const LAUNCHES_QUREY = gql
` query LaunchesQurey{
    launches{
        flight_number
        mission_name
        launch_date_local
        launch_success
    }
} `;
export class Launches extends Component {
    render() {
        return (
            <Fragment>
              <h1 className="display-4 my-2 text-white">Launches</h1>
              <MissionKey/> 
              <Query query={LAUNCHES_QUREY}>
                  {
                      ({loading, error, data}) => {
                          if(loading) return <h4 className="text-white">Loading...</h4>;
                          if(error) 
                          console.log(data)

                          return(

                           <Fragment>
                              {
                             data.launches.map(launch=>(
                             <LaunchesItem key={launch.flight_number} launch={launch}/>
                                  ))
                              }
                          </Fragment>
                          )
                      }
                  }
            </Query> 
            </Fragment>
        )
    }
}

export default Launches
